    border: 1px solid black;
}

.loginForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  margin-top: 50%;

  width: 320px;
  min-height: 300px;

  padding: 2rem;
  border: 1px solid #ccc;
  border-radius: 12px;

  background: #fff;
  box-shadow: 0 8px 20px rgba(0,0,0,0.1);

  margin: 1rem auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Input fields */
.loginForm input {
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #bbb;
  border-radius: 8px;
  font-size: 0.95rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.loginForm input:focus {
  border-color: #5563DE;
  outline: none;
  box-shadow: 0 0 5px rgba(85, 99, 222, 0.3);
}

/* Error messages */
.loginForm div {
  color: #d9534f;
  font-size: 0.85rem;
  margin: -0.5rem 0 0.75rem 0;
}

/* Submit button */
.loginForm button {
  padding: 0.75rem;
  background: #5563DE;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s;
}

.loginForm button:disabled {
  background: #a3a8e3;
  cursor: not-allowed;
}

.loginForm button:hover:enabled {
  background: #3f4bb8;
}
