.App {
  text-align: center;
}

html {
  margin: 0;
  padding: 0;
  background: #1f2794;
  background: linear-gradient(90deg, rgba(31, 39, 148, 1) 0%, rgba(222, 73, 172, 1) 62%, rgba(255, 128, 0, 1) 100%);
}

body {
   margin: 0px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
